import axios from "axios";
const baseURL = "http://localhost:7000/";

// 從 localStorage 中獲取 token
function getLocalToken() {
    const token = window.localStorage.getItem("token");
    return token;
}

// 創建一個 axios 實例
export const request = axios.create({
    baseURL,
    headers: {
        authorization: getLocalToken(), // headers 塞 token
    },
});

// user
export const getUserList = (params) => request.get("/getUserList", { params });
export const postUserLogin = (data) => request.post("/signin", data);
export const postUserSignup = (data) => request.post("/signup", data);
export const getUserStatusOfNft = (data) => request.post("/getUserStatusOfNft", data);